import React, { useState, useEffect } from 'react'
import { useParams, useNavigate } from 'react-router-dom'
import {
  Box,
  Typography,
  Card,
  CardContent,
  Grid,
  Button,
  Chip,
  Stack,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  LinearProgress,
  Alert,
  Divider,
  IconButton,
  Tooltip,
  Menu,
  MenuItem
} from '@mui/material'
import {
  Edit as EditIcon,
  TrendingUp as TrendingUpIcon,
  Assessment as AssessmentIcon,
  Description as ReportIcon,
  MoreVert as MoreVertIcon,
  Timeline as TimelineIcon,
  LocationOn as LocationIcon,
  Business as BusinessIcon,
  Schedule as ScheduleIcon
} from '@mui/icons-material'

interface Project {
  id: string
  name: string
  industry_type: string
  target_area: string
  description: string
  status: 'active' | 'completed' | 'paused'
  created_at: string
  updated_at: string
}

interface SimulationHistory {
  id: string
  created_at: string
  target_monthly_sales: number
  required_budget: number
  breakeven_months: number
  selected_media: string[]
}

interface AnalysisResult {
  type: 'demographics' | 'competitors' | 'demand'
  status: 'completed' | 'in_progress' | 'failed'
  completed_at?: string
  summary: string
}

const industryTypes = [
  { value: 'beauty', label: '' },
  { value: 'restaurant', label: '' },
  { value: 'retail', label: 'EC' },
  { value: 'healthcare', label: '' },
  { value: 'education', label: '' },
  { value: 'real_estate', label: '' },
  { value: 'financial', label: '' },
  { value: 'automotive', label: '' }
]

const statusColors = {
  active: 'success',
  completed: 'primary', 
  paused: 'warning'
} as const

const statusLabels = {
  active: '',
  completed: '',
  paused: ''
} as const

function ProjectDetail() {
  const { projectId } = useParams()
  const navigate = useNavigate()
  const [project, setProject] = useState<Project | null>(null)
  const [simulationHistory, setSimulationHistory] = useState<SimulationHistory[]>([])
  const [analysisResults, setAnalysisResults] = useState<AnalysisResult[]>([])
  const [loading, setLoading] = useState(true)
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null)

  useEffect(() => {
    if (projectId) {
      fetchProjectData()
    }
  }, [projectId])

  const fetchProjectData = async () => {
    try {
      // 
      const response = await fetch(`/api/projects/${projectId}`)
      if (response.ok) {
        const projectData = await response.json()
        setProject(projectData)
      } else {
        // 
        setProject({
          id: projectId!,
          name: '',
          industry_type: 'beauty',
          target_area: '',
          description: '',
          status: 'active',
          created_at: '2024-01-15T09:00:00Z',
          updated_at: '2024-01-20T15:30:00Z'
        })
      }

      // 
      const simResponse = await fetch(`/api/projects/${projectId}/simulations`)
      if (simResponse.ok) {
        const simData = await simResponse.json()
        setSimulationHistory(simData)
      } else {
        // 
        setSimulationHistory([
          {
            id: '1',
            created_at: '2024-01-20T10:30:00Z',
            target_monthly_sales: 1000000,
            required_budget: 450000,
            breakeven_months: 6,
            selected_media: ['google_ads', 'facebook_ads']
          },
          {
            id: '2',
            created_at: '2024-01-18T14:20:00Z',
            target_monthly_sales: 800000,
            required_budget: 320000,
            breakeven_months: 8,
            selected_media: ['google_ads']
          },
          {
            id: '3',
            created_at: '2024-01-16T09:15:00Z',
            target_monthly_sales: 1200000,
            required_budget: 540000,
            breakeven_months: 5,
            selected_media: ['google_ads', 'facebook_ads', 'instagram_ads']
          }
        ])
      }

      // 
      const analysisResponse = await fetch(`/api/projects/${projectId}/analyses`)
      if (analysisResponse.ok) {
        const analysisData = await analysisResponse.json()
        setAnalysisResults(analysisData)
      } else {
        // 
        setAnalysisResults([
          {
            type: 'demographics',
            status: 'completed',
            completed_at: '2024-01-19T11:20:00Z',
            summary: ' 45,82330-4027.2%'
          },
          {
            type: 'competitors',
            status: 'completed',
            completed_at: '2024-01-19T13:45:00Z',
            summary: '1km34,000-8,000'
          },
          {
            type: 'demand',
            status: 'completed',
            completed_at: '2024-01-19T16:10:00Z',
            summary: '890M12125%'
          }
        ])
      }
    } catch (error) {
      console.error(':', error)
    } finally {
      setLoading(false)
    }
  }

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('ja-JP', {
      year: 'numeric',
      month: 'short',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    })
  }

  const formatCurrency = (amount: number) => `${amount.toLocaleString()}`

  const getIndustryLabel = (value: string) => {
    return industryTypes.find(i => i.value === value)?.label || value
  }

  const getAnalysisTypeLabel = (type: string) => {
    const labels = {
      demographics: '',
      competitors: '',
      demand: ''
    }
    return labels[type as keyof typeof labels] || type
  }

  const handleMenuOpen = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget)
  }

  const handleMenuClose = () => {
    setAnchorEl(null)
  }

  if (loading) {
    return (
      <Box>
        <Typography variant="h6" gutterBottom>...</Typography>
        <LinearProgress />
      </Box>
    )
  }

  if (!project) {
    return (
      <Alert severity="error">
        
      </Alert>
    )
  }

  return (
    <Box>
      {/*  */}
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
        <Box>
          <Typography variant="h1" gutterBottom>
            {project.name}
          </Typography>
          <Stack direction="row" spacing={2} alignItems="center">
            <Chip
              label={statusLabels[project.status]}
              color={statusColors[project.status]}
              size="small"
            />
            <Typography variant="body2" color="text.secondary">
              ID: {project.id}
            </Typography>
          </Stack>
        </Box>
        <Stack direction="row" spacing={2}>
          <Button
            variant="contained"
            startIcon={<TrendingUpIcon />}
            onClick={() => navigate(`/simulator/${projectId}`)}
          >
            
          </Button>
          <IconButton onClick={handleMenuOpen}>
            <MoreVertIcon />
          </IconButton>
        </Stack>
      </Box>

      <Grid container spacing={3}>
        {/*  */}
        <Grid item xs={12} md={8}>
          <Stack spacing={3}>
            {/*  */}
            <Card>
              <CardContent>
                <Typography variant="h6" gutterBottom>
                  
                </Typography>
                <Grid container spacing={2}>
                  <Grid item xs={12} sm={6}>
                    <Box sx={{ display: 'flex', alignItems: 'center', gap: 1, mb: 2 }}>
                      <BusinessIcon color="primary" />
                      <Box>
                        <Typography variant="caption" color="text.secondary"></Typography>
                        <Typography variant="body2" fontWeight="medium">
                          {getIndustryLabel(project.industry_type)}
                        </Typography>
                      </Box>
                    </Box>
                  </Grid>
                  <Grid item xs={12} sm={6}>
                    <Box sx={{ display: 'flex', alignItems: 'center', gap: 1, mb: 2 }}>
                      <LocationIcon color="primary" />
                      <Box>
                        <Typography variant="caption" color="text.secondary"></Typography>
                        <Typography variant="body2" fontWeight="medium">
                          {project.target_area}
                        </Typography>
                      </Box>
                    </Box>
                  </Grid>
                  <Grid item xs={12} sm={6}>
                    <Box sx={{ display: 'flex', alignItems: 'center', gap: 1, mb: 2 }}>
                      <ScheduleIcon color="primary" />
                      <Box>
                        <Typography variant="caption" color="text.secondary"></Typography>
                        <Typography variant="body2" fontWeight="medium">
                          {formatDate(project.created_at)}
                        </Typography>
                      </Box>
                    </Box>
                  </Grid>
                  <Grid item xs={12} sm={6}>
                    <Box sx={{ display: 'flex', alignItems: 'center', gap: 1, mb: 2 }}>
                      <ScheduleIcon color="primary" />
                      <Box>
                        <Typography variant="caption" color="text.secondary"></Typography>
                        <Typography variant="body2" fontWeight="medium">
                          {formatDate(project.updated_at)}
                        </Typography>
                      </Box>
                    </Box>
                  </Grid>
                </Grid>
                <Divider sx={{ my: 2 }} />
                <Typography variant="body2" paragraph>
                  {project.description}
                </Typography>
              </CardContent>
            </Card>

            {/*  */}
            <Card>
              <CardContent>
                <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
                  <Typography variant="h6">
                    
                  </Typography>
                  <Button
                    size="small"
                    startIcon={<TrendingUpIcon />}
                    onClick={() => navigate(`/simulator/${projectId}`)}
                  >
                    
                  </Button>
                </Box>
                
                {simulationHistory.length === 0 ? (
                  <Alert severity="info">
                    
                  </Alert>
                ) : (
                  <TableContainer>
                    <Table>
                      <TableHead>
                        <TableRow>
                          <TableCell></TableCell>
                          <TableCell align="right"></TableCell>
                          <TableCell align="right"></TableCell>
                          <TableCell align="center"></TableCell>
                          <TableCell></TableCell>
                        </TableRow>
                      </TableHead>
                      <TableBody>
                        {simulationHistory.map((sim, index) => (
                          <TableRow key={sim.id}>
                            <TableCell>
                              <Typography variant="body2">
                                {formatDate(sim.created_at)}
                              </Typography>
                            </TableCell>
                            <TableCell align="right">
                              <Typography variant="body2" fontWeight="medium">
                                {formatCurrency(sim.target_monthly_sales)}
                              </Typography>
                            </TableCell>
                            <TableCell align="right">
                              <Typography variant="body2" fontWeight="medium">
                                {formatCurrency(sim.required_budget)}
                              </Typography>
                            </TableCell>
                            <TableCell align="center">
                              <Chip
                                label={`${sim.breakeven_months}`}
                                size="small"
                                color={sim.breakeven_months <= 6 ? 'success' : sim.breakeven_months <= 12 ? 'warning' : 'error'}
                              />
                            </TableCell>
                            <TableCell>
                              <Stack direction="row" spacing={0.5}>
                                {sim.selected_media.map(media => (
                                  <Chip
                                    key={media}
                                    label={media.replace('_ads', '')}
                                    size="small"
                                    variant="outlined"
                                  />
                                ))}
                              </Stack>
                            </TableCell>
                          </TableRow>
                        ))}
                      </TableBody>
                    </Table>
                  </TableContainer>
                )}
              </CardContent>
            </Card>
          </Stack>
        </Grid>

        {/*  */}
        <Grid item xs={12} md={4}>
          <Stack spacing={3}>
            {/*  */}
            <Card>
              <CardContent>
                <Typography variant="h6" gutterBottom>
                  
                </Typography>
                <Stack spacing={2}>
                  <Button
                    variant="outlined"
                    startIcon={<AssessmentIcon />}
                    onClick={() => navigate(`/analysis/${projectId}`)}
                    fullWidth
                  >
                    
                  </Button>
                  <Button
                    variant="outlined"
                    startIcon={<ReportIcon />}
                    onClick={() => navigate(`/reports/${projectId}`)}
                    fullWidth
                  >
                    
                  </Button>
                  <Button
                    variant="outlined"
                    startIcon={<EditIcon />}
                    onClick={() => navigate(`/projects`)}
                    fullWidth
                  >
                    
                  </Button>
                </Stack>
              </CardContent>
            </Card>

            {/*  */}
            <Card>
              <CardContent>
                <Typography variant="h6" gutterBottom>
                  
                </Typography>
                {analysisResults.length === 0 ? (
                  <Alert severity="info">
                    
                  </Alert>
                ) : (
                  <Stack spacing={2}>
                    {analysisResults.map((analysis, index) => (
                      <Card key={index} variant="outlined">
                        <CardContent sx={{ pb: 2 }}>
                          <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 1 }}>
                            <Typography variant="subtitle2">
                              {getAnalysisTypeLabel(analysis.type)}
                            </Typography>
                            <Chip
                              label={analysis.status === 'completed' ? '' : ''}
                              size="small"
                              color={analysis.status === 'completed' ? 'success' : 'default'}
                            />
                          </Box>
                          <Typography variant="caption" color="text.secondary" paragraph>
                            {analysis.summary}
                          </Typography>
                          {analysis.completed_at && (
                            <Typography variant="caption" color="text.secondary">
                              : {formatDate(analysis.completed_at)}
                            </Typography>
                          )}
                        </CardContent>
                      </Card>
                    ))}
                  </Stack>
                )}
              </CardContent>
            </Card>

            {/*  */}
            {simulationHistory.length > 0 && (
              <Card>
                <CardContent>
                  <Typography variant="h6" gutterBottom>
                    
                  </Typography>
                  <Grid container spacing={2}>
                    <Grid item xs={12}>
                      <Box textAlign="center">
                        <Typography variant="h4" color="primary">
                          {formatCurrency(simulationHistory[0].target_monthly_sales)}
                        </Typography>
                        <Typography variant="caption"></Typography>
                      </Box>
                    </Grid>
                    <Grid item xs={6}>
                      <Box textAlign="center">
                        <Typography variant="h5" color="error">
                          {formatCurrency(simulationHistory[0].required_budget)}
                        </Typography>
                        <Typography variant="caption"></Typography>
                      </Box>
                    </Grid>
                    <Grid item xs={6}>
                      <Box textAlign="center">
                        <Typography variant="h5" color="warning.main">
                          {simulationHistory[0].breakeven_months}
                        </Typography>
                        <Typography variant="caption"></Typography>
                      </Box>
                    </Grid>
                  </Grid>
                  <Button
                    variant="contained"
                    fullWidth
                    sx={{ mt: 2 }}
                    onClick={() => navigate(`/simulator/${projectId}`)}
                  >
                    
                  </Button>
                </CardContent>
              </Card>
            )}
          </Stack>
        </Grid>
      </Grid>

      {/*  */}
      <Menu
        anchorEl={anchorEl}
        open={Boolean(anchorEl)}
        onClose={handleMenuClose}
      >
        <MenuItem onClick={() => {
          navigate(`/analysis/${projectId}`)
          handleMenuClose()
        }}>
          <AssessmentIcon sx={{ mr: 1 }} />
          
        </MenuItem>
        <MenuItem onClick={() => {
          navigate(`/reports/${projectId}`)
          handleMenuClose()
        }}>
          <ReportIcon sx={{ mr: 1 }} />
          
        </MenuItem>
        <MenuItem onClick={() => {
          // TODO: 
          handleMenuClose()
        }}>
          <EditIcon sx={{ mr: 1 }} />
          
        </MenuItem>
      </Menu>
    </Box>
  )
}

export default ProjectDetail 