import React, { useState, useEffect } from 'react'
import { useNavigate } from 'react-router-dom'
import {
  Box,
  Typography,
  Card,
  CardContent,
  CardActions,
  Grid,
  Button,
  TextField,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Chip,
  Stack,
  IconButton,
  Menu,
  Tooltip,
  LinearProgress,
  Alert,
  CircularProgress
} from '@mui/material'
import {
  Add as AddIcon,
  Edit as EditIcon,
  Delete as DeleteIcon,
  MoreVert as MoreVertIcon,
  Search as SearchIcon,
  Timeline as TimelineIcon,
  TrendingUp as TrendingUpIcon,
  FilterList as FilterIcon,
  Business as BusinessIcon,
  LocationOn as LocationOnIcon,
  Analytics as AnalyticsIcon
} from '@mui/icons-material'

interface Project {
  id: string
  name: string
  industry_type: string
  target_area: string
  description: string
  status: 'active' | 'completed' | 'paused'
  created_at: string
  updated_at: string
  latest_simulation?: {
    target_monthly_sales: number
    required_budget: number
    breakeven_months: number
  }
}

interface ProjectFormData {
  name: string
  industry_type: string
  target_area: string
  description: string
}

const industryTypes = [
  { value: 'beauty', label: '' },
  { value: 'restaurant', label: '' },
  { value: 'retail', label: 'EC' },
  { value: 'healthcare', label: '' },
  { value: 'education', label: '' },
  { value: 'real_estate', label: '' },
  { value: 'financial', label: '' },
  { value: 'automotive', label: '' }
]

const statusColors = {
  active: 'success',
  completed: 'primary',
  paused: 'warning'
} as const

const statusLabels = {
  active: '',
  completed: '',
  paused: ''
} as const

function ProjectList() {
  const navigate = useNavigate()
  const [projects, setProjects] = useState<Project[]>([])
  const [loading, setLoading] = useState(true)
  const [searchTerm, setSearchTerm] = useState('')
  const [filterStatus, setFilterStatus] = useState<string>('all')
  const [filterIndustry, setFilterIndustry] = useState<string>('all')
  
  // /
  const [dialogOpen, setDialogOpen] = useState(false)
  const [editingProject, setEditingProject] = useState<Project | null>(null)
  const [formData, setFormData] = useState<ProjectFormData>({
    name: '',
    industry_type: 'beauty',
    target_area: '',
    description: ''
  })
  
  // 
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null)
  const [selectedProject, setSelectedProject] = useState<Project | null>(null)

  // 
  useEffect(() => {
    fetchProjects()
  }, [])

  const fetchProjects = async () => {
    try {
      const response = await fetch('/api/projects')
      if (response.ok) {
        const data = await response.json()
        setProjects(data)
      } else {
        console.error('')
      }
    } catch (error) {
      console.error(':', error)
      // 
      setProjects([
        {
          id: '1',
          name: '',
          industry_type: 'beauty',
          target_area: '',
          description: '',
          status: 'active',
          created_at: '2024-01-15T09:00:00Z',
          updated_at: '2024-01-20T15:30:00Z',
          latest_simulation: {
            target_monthly_sales: 1000000,
            required_budget: 450000,
            breakeven_months: 6
          }
        },
        {
          id: '2',
          name: '',
          industry_type: 'restaurant',
          target_area: '',
          description: '',
          status: 'completed',
          created_at: '2023-12-01T10:00:00Z',
          updated_at: '2024-01-10T12:00:00Z',
          latest_simulation: {
            target_monthly_sales: 800000,
            required_budget: 320000,
            breakeven_months: 8
          }
        }
      ])
    } finally {
      setLoading(false)
    }
  }

  // 
  const filteredProjects = projects.filter(project => {
    const matchesSearch = project.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         project.description.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         project.target_area.toLowerCase().includes(searchTerm.toLowerCase())
    
    const matchesStatus = filterStatus === 'all' || project.status === filterStatus
    const matchesIndustry = filterIndustry === 'all' || project.industry_type === filterIndustry
    
    return matchesSearch && matchesStatus && matchesIndustry
  })

  // /
  const handleSubmitProject = async () => {
    try {
      const url = editingProject ? `/api/projects/${editingProject.id}` : '/api/projects'
      const method = editingProject ? 'PUT' : 'POST'
      
      const response = await fetch(url, {
        method,
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(formData)
      })
      
      if (response.ok) {
        await fetchProjects()
        handleCloseDialog()
      } else {
        console.error('')
      }
    } catch (error) {
      console.error(':', error)
    }
  }

  // 
  const handleDeleteProject = async (projectId: string) => {
    if (!window.confirm('')) return
    
    try {
      const response = await fetch(`/api/projects/${projectId}`, {
        method: 'DELETE'
      })
      
      if (response.ok) {
        await fetchProjects()
      } else {
        console.error('')
      }
    } catch (error) {
      console.error(':', error)
    }
  }

  const handleOpenCreateDialog = () => {
    setEditingProject(null)
    setFormData({
      name: '',
      industry_type: 'beauty',
      target_area: '',
      description: ''
    })
    setDialogOpen(true)
  }

  const handleOpenEditDialog = (project: Project) => {
    setEditingProject(project)
    setFormData({
      name: project.name,
      industry_type: project.industry_type,
      target_area: project.target_area,
      description: project.description
    })
    setDialogOpen(true)
  }

  const handleCloseDialog = () => {
    setDialogOpen(false)
    setEditingProject(null)
  }

  const handleMenuOpen = (event: React.MouseEvent<HTMLElement>, project: Project) => {
    setAnchorEl(event.currentTarget)
    setSelectedProject(project)
  }

  const handleMenuClose = () => {
    setAnchorEl(null)
    setSelectedProject(null)
  }

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('ja-JP')
  }

  const formatCurrency = (amount: number) => `${amount.toLocaleString()}`

  // 
  const renderProjectCard = (project: Project) => (
    <Card 
      key={project.id} 
      sx={{ 
        height: '100%',
        display: 'flex',
        flexDirection: 'column',
        transition: 'all 0.3s ease',
        '&:hover': {
          transform: 'translateY(-4px)',
          boxShadow: 4,
        },
        position: 'relative',
        border: '1px solid',
        borderColor: project.status === 'active' ? 'primary.main' : 
                   project.status === 'completed' ? 'success.main' : 'warning.main'
      }}
    >
      {/*  */}
      <Box sx={{ position: 'absolute', top: 12, right: 12, zIndex: 1 }}>
        <Chip
          label={project.status === 'active' ? '' : 
                 project.status === 'completed' ? '' : ''}
          color={project.status === 'active' ? 'primary' : 
                 project.status === 'completed' ? 'success' : 'warning'}
          size="small"
          variant="filled"
        />
      </Box>

      <CardContent sx={{ flex: 1, pb: 1 }}>
        {/*  */}
        <Typography variant="h6" component="h3" gutterBottom sx={{ pr: 8 }}>
          {project.name}
        </Typography>

        {/*  */}
        <Box sx={{ display: 'flex', gap: 1, mb: 2 }}>
          <Chip 
            label={getIndustryLabel(project.industry_type)}
            variant="outlined"
            size="small"
            icon={<BusinessIcon />}
          />
          <Chip 
            label={project.target_area}
            variant="outlined"
            size="small"
            icon={<LocationOnIcon />}
          />
        </Box>

        {/*  */}
        <Typography 
          variant="body2" 
          color="text.secondary" 
          sx={{ 
            mb: 2,
            display: '-webkit-box',
            WebkitLineClamp: 2,
            WebkitBoxOrient: 'vertical',
            overflow: 'hidden',
            minHeight: '2.5em'
          }}
        >
          {project.description || ''}
        </Typography>

        {/*  */}
        <Box sx={{ display: 'flex', flexDirection: 'column', gap: 0.5 }}>
          <Typography variant="caption" color="text.secondary">
            : {formatDate(project.created_at)}
          </Typography>
          {project.updated_at && project.updated_at !== project.created_at && (
            <Typography variant="caption" color="text.secondary">
              : {formatDate(project.updated_at)}
            </Typography>
          )}
        </Box>

        {/*  */}
        {project.latest_simulation && (
          <Box sx={{ mt: 2, p: 1.5, bgcolor: 'grey.50', borderRadius: 1 }}>
            <Typography variant="caption" color="text.secondary" gutterBottom>
              
            </Typography>
            <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
              <Typography variant="body2" fontWeight="medium">
                : {formatCurrency(project.latest_simulation.target_monthly_sales)}
              </Typography>
              <Typography variant="body2" color="primary.main">
                {project.latest_simulation.breakeven_months}
              </Typography>
            </Box>
          </Box>
        )}
      </CardContent>

      {/*  */}
      <CardActions sx={{ justifyContent: 'space-between', px: 2, pb: 2 }}>
        <Button
          size="small"
          variant="outlined"
          startIcon={<AnalyticsIcon />}
          onClick={() => {/* TODO:  */}}
        >
          
        </Button>
        <Box>
          <IconButton
            size="small"
            onClick={(e) => handleMenuOpen(e, project)}
            sx={{ ml: 1 }}
          >
            <MoreVertIcon />
          </IconButton>
        </Box>
      </CardActions>
    </Card>
  )

  // 
  const getIndustryLabel = (industry: string) => {
    const labels: { [key: string]: string } = {
      'beauty': '',
      'restaurant': '',
      'retail': '',
      'healthcare': '',
      'education': '',
      'it': 'IT',
      'consulting': '',
      'finance': '',
      'real_estate': '',
      'other': ''
    }
    return labels[industry] || industry
  }

  if (loading) {
    return (
      <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', minHeight: '400px' }}>
        <CircularProgress size={60} />
        <Typography variant="h6" sx={{ ml: 2 }}>
          ...
        </Typography>
      </Box>
    )
  }

  return (
    <Box>
      {/*  */}
      <Box sx={{ 
        display: 'flex', 
        justifyContent: 'space-between', 
        alignItems: 'center', 
        mb: 4,
        p: 3,
        bgcolor: 'primary.main',
        color: 'white',
        borderRadius: 2,
        boxShadow: 2
      }}>
        <Box>
          <Typography variant="h4" gutterBottom sx={{ fontWeight: 'bold' }}>
            
          </Typography>
          <Typography variant="body1" sx={{ opacity: 0.9 }}>
            
          </Typography>
        </Box>
        <Button
          variant="contained"
          startIcon={<AddIcon />}
          onClick={handleOpenCreateDialog}
          size="large"
          sx={{ 
            bgcolor: 'white',
            color: 'primary.main',
            '&:hover': {
              bgcolor: 'grey.100'
            },
            px: 3,
            py: 1.5
          }}
        >
          
        </Button>
      </Box>

      {/*  */}
      <Card sx={{ mb: 3, boxShadow: 2 }}>
        <CardContent>
          <Typography variant="h6" gutterBottom sx={{ display: 'flex', alignItems: 'center' }}>
            <FilterIcon sx={{ mr: 1 }} />
            
          </Typography>
          <Grid container spacing={3} alignItems="center">
            <Grid item xs={12} md={5}>
              <TextField
                fullWidth
                placeholder="..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                InputProps={{
                  startAdornment: <SearchIcon sx={{ mr: 1, color: 'text.secondary' }} />
                }}
                variant="outlined"
              />
            </Grid>
            <Grid item xs={12} md={3}>
              <FormControl fullWidth>
                <InputLabel></InputLabel>
                <Select
                  value={filterStatus}
                  onChange={(e) => setFilterStatus(e.target.value)}
                  label=""
                >
                  <MenuItem value="all"></MenuItem>
                  <MenuItem value="active"> </MenuItem>
                  <MenuItem value="completed"> </MenuItem>
                  <MenuItem value="paused"> </MenuItem>
                </Select>
              </FormControl>
            </Grid>
            <Grid item xs={12} md={3}>
              <FormControl fullWidth>
                <InputLabel></InputLabel>
                <Select
                  value={filterIndustry}
                  onChange={(e) => setFilterIndustry(e.target.value)}
                  label=""
                >
                  <MenuItem value="all"></MenuItem>
                  <MenuItem value="beauty"></MenuItem>
                  <MenuItem value="restaurant"></MenuItem>
                  <MenuItem value="retail"></MenuItem>
                  <MenuItem value="healthcare"></MenuItem>
                  <MenuItem value="education"></MenuItem>
                  <MenuItem value="it">IT</MenuItem>
                  <MenuItem value="consulting"></MenuItem>
                  <MenuItem value="finance"></MenuItem>
                  <MenuItem value="real_estate"></MenuItem>
                  <MenuItem value="other"></MenuItem>
                </Select>
              </FormControl>
            </Grid>
            <Grid item xs={12} md={1}>
              <Typography variant="body2" color="text.secondary" textAlign="center">
                {filteredProjects.length} 
              </Typography>
            </Grid>
          </Grid>
        </CardContent>
      </Card>

      {/*  */}
      {filteredProjects.length === 0 ? (
        <Box sx={{ 
          textAlign: 'center', 
          py: 8,
          bgcolor: 'grey.50',
          borderRadius: 2,
          border: '2px dashed',
          borderColor: 'grey.300'
        }}>
          <BusinessIcon sx={{ fontSize: 64, color: 'grey.400', mb: 2 }} />
          <Typography variant="h6" color="text.secondary" gutterBottom>
            {searchTerm || filterStatus !== 'all' || filterIndustry !== 'all' 
              ? '' 
              : ''}
          </Typography>
          <Typography variant="body2" color="text.secondary" sx={{ mb: 3 }}>
            
          </Typography>
          <Button
            variant="contained"
            startIcon={<AddIcon />}
            onClick={handleOpenCreateDialog}
            size="large"
          >
            
          </Button>
        </Box>
      ) : (
        <Grid container spacing={3}>
          {filteredProjects.map(renderProjectCard)}
        </Grid>
      )}

      {/*  */}
      <Dialog 
        open={dialogOpen} 
        onClose={handleCloseDialog} 
        maxWidth="md" 
        fullWidth
        PaperProps={{
          sx: { borderRadius: 3 }
        }}
      >
        <DialogTitle sx={{ 
          pb: 1,
          display: 'flex',
          alignItems: 'center',
          bgcolor: 'primary.main',
          color: 'white',
          mb: 3
        }}>
          <AddIcon sx={{ mr: 2 }} />
          {editingProject ? '' : ''}
        </DialogTitle>
        
        <DialogContent sx={{ px: 4, py: 3 }}>
          <Grid container spacing={3}>
            {/*  */}
            <Grid item xs={12}>
              <TextField
                autoFocus
                label=""
                fullWidth
                value={formData.name}
                onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                placeholder=":  "
                required
                error={!formData.name}
                helperText={!formData.name ? '' : ''}
                variant="outlined"
              />
            </Grid>

            {/*  */}
            <Grid item xs={12} md={6}>
              <FormControl fullWidth required>
                <InputLabel></InputLabel>
                <Select
                  value={formData.industry_type}
                  onChange={(e) => setFormData({ ...formData, industry_type: e.target.value })}
                  label=""
                >
                  <MenuItem value="beauty">
                    <Box sx={{ display: 'flex', alignItems: 'center' }}>
                       
                    </Box>
                  </MenuItem>
                  <MenuItem value="restaurant">
                    <Box sx={{ display: 'flex', alignItems: 'center' }}>
                       
                    </Box>
                  </MenuItem>
                  <MenuItem value="retail">
                    <Box sx={{ display: 'flex', alignItems: 'center' }}>
                       
                    </Box>
                  </MenuItem>
                  <MenuItem value="healthcare">
                    <Box sx={{ display: 'flex', alignItems: 'center' }}>
                       
                    </Box>
                  </MenuItem>
                  <MenuItem value="education">
                    <Box sx={{ display: 'flex', alignItems: 'center' }}>
                       
                    </Box>
                  </MenuItem>
                  <MenuItem value="it">
                    <Box sx={{ display: 'flex', alignItems: 'center' }}>
                       IT
                    </Box>
                  </MenuItem>
                  <MenuItem value="consulting">
                    <Box sx={{ display: 'flex', alignItems: 'center' }}>
                       
                    </Box>
                  </MenuItem>
                  <MenuItem value="finance">
                    <Box sx={{ display: 'flex', alignItems: 'center' }}>
                       
                    </Box>
                  </MenuItem>
                  <MenuItem value="real_estate">
                    <Box sx={{ display: 'flex', alignItems: 'center' }}>
                       
                    </Box>
                  </MenuItem>
                  <MenuItem value="other">
                    <Box sx={{ display: 'flex', alignItems: 'center' }}>
                       
                    </Box>
                  </MenuItem>
                </Select>
              </FormControl>
            </Grid>

            {/*  */}
            <Grid item xs={12} md={6}>
              <TextField
                label=""
                fullWidth
                value={formData.target_area}
                onChange={(e) => setFormData({ ...formData, target_area: e.target.value })}
                placeholder=": "
                required
                error={!formData.target_area}
                helperText={!formData.target_area ? '' : ''}
                variant="outlined"
                InputProps={{
                  startAdornment: <LocationOnIcon sx={{ mr: 1, color: 'text.secondary' }} />
                }}
              />
            </Grid>

            {/*  */}
            <Grid item xs={12}>
              <TextField
                label=""
                fullWidth
                multiline
                rows={4}
                value={formData.description}
                onChange={(e) => setFormData({ ...formData, description: e.target.value })}
                placeholder="&#10;: 52030"
                variant="outlined"
                helperText=""
              />
            </Grid>

            {/*  */}
            <Grid item xs={12}>
              <Card sx={{ bgcolor: 'grey.50', border: '1px solid', borderColor: 'grey.300' }}>
                <CardContent>
                  <Typography variant="h6" gutterBottom sx={{ display: 'flex', alignItems: 'center' }}>
                     
                  </Typography>
                  <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 1, mb: 2 }}>
                    {formData.name && (
                      <Chip label={` ${formData.name}`} variant="outlined" />
                    )}
                    {formData.industry_type && (
                      <Chip label={getIndustryLabel(formData.industry_type)} variant="outlined" />
                    )}
                    {formData.target_area && (
                      <Chip label={` ${formData.target_area}`} variant="outlined" />
                    )}
                  </Box>
                  <Typography variant="body2" color="text.secondary">
                    {formData.description || ''}
                  </Typography>
                </CardContent>
              </Card>
            </Grid>
          </Grid>
        </DialogContent>
        
        <DialogActions sx={{ px: 4, py: 3, gap: 2 }}>
          <Button 
            onClick={handleCloseDialog} 
            variant="outlined"
            size="large"
          >
            
          </Button>
          <Button 
            onClick={handleSubmitProject} 
            variant="contained"
            disabled={!formData.name || !formData.target_area}
            size="large"
            startIcon={editingProject ? <EditIcon /> : <AddIcon />}
          >
            {editingProject ? '' : ''}
          </Button>
        </DialogActions>
      </Dialog>

      {/*  */}
      <Menu
        anchorEl={anchorEl}
        open={Boolean(anchorEl)}
        onClose={handleMenuClose}
      >
        <MenuItem 
          onClick={() => {
            if (selectedProject) {
              handleOpenEditDialog(selectedProject)
            }
            handleMenuClose()
          }}
        >
          <EditIcon sx={{ mr: 1 }} />
          
        </MenuItem>
        <MenuItem 
          onClick={() => {
            if (selectedProject) {
              handleDeleteProject(selectedProject.id)
            }
            handleMenuClose()
          }}
        >
          <DeleteIcon sx={{ mr: 1 }} />
          
        </MenuItem>
      </Menu>
    </Box>
  )
}

export default ProjectList 